@use '../abstracts' as *;
@use 'sass:color';

.image-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 10000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.85);
  box-sizing: border-box;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition:
    opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1),
    visibility 0s linear 0.4s;

  &.is-visible {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transition-delay: 0s;
  }

  &__content {
    margin: 0 auto;
    display: block;
    max-width: 90%;
    max-height: 85vh;
    transform: scale(0.8);
    opacity: 0;
    transition:
      transform 0.4s cubic-bezier(0.4, 0, 0.2, 1) 0.05s,
      opacity 0.3s ease 0.05s;
    will-change: transform, opacity;
  }

  &.is-visible &__content {
    transform: scale(1);
    opacity: 1;
  }

  &__caption {
    margin: 15px auto 0;
    display: block;
    width: 80%;
    max-width: 700px;
    text-align: center;
    color: #ccc;
    padding: 10px 0;
    font-size: 0.9em;
    opacity: 0;
    transition: opacity 0.3s ease 0.2s;
  }

  &.is-visible &__caption {
    opacity: 1;
  }

  &__close-button {
    position: absolute;
    top: 20px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition:
      color 0.3s ease,
      opacity 0.3s ease 0.2s;
    cursor: pointer;
    opacity: 0;

    &:hover,
    &:focus {
      color: #bbb;
      text-decoration: none;
    }
  }

  &.is-visible &__close-button {
    opacity: 1;
  }
}
