@use '../abstracts' as *;


  .news-list {
    list-style: none;
    padding: 0;
    margin: 0;

    &__item {
      position: relative;
      margin-bottom: 1.5rem;
      background-color: $color-white;
      border-radius: 8px;
      box-shadow: 0 4px 15px rgba($color-dark, 0.08);
      @include transition(transform, box-shadow);

      &:last-child {
        margin-bottom: 0;
      }

      &:hover {
        transform: translateY(-5px);
        box-shadow: 0 6px 20px rgba($color-dark, 0.12);
        background-color: $color-light-bg;
      }

      // 区切り線
      &::after {
        position: absolute;
        content: '';
        display: block;
        bottom: -1.5rem;
        left: 0;
        width: 100%;
        height: 1px;
        border-bottom: 1px dotted $color-gray-line;
      }

      &:nth-child(n + 2) {
        margin-top: 3rem;
      }
    }

    &__link {
      text-decoration: none;
      color: $color-dark;

      &:hover {
        color: $color-dark;
        text-decoration: none;
      }
    }
    &__inner {
      display: flex;
      align-items: flex-start;
      padding: 2rem;

      @include sp {
        flex-direction: column;
        padding: 1.5rem;
      }
    }

    &__image-wrapper {
      flex-shrink: 0;
      width: 150px;
      height: 100px;
      margin-right: 2.5rem;
      border-radius: 6px;
      overflow: hidden;

      @include sp {
        width: 100%;
        height: auto;
        max-height: 180px;
        margin-right: 0;
        margin-bottom: 1.5rem;
      }

      img.news-list__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &__content {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &__date {
      display: block;
      font-size: $font-size-small;
      color: $color-gray-text;
      margin-bottom: 0.8rem;
      font-family: $font-roboto;
    }

    &__title {
      font-family: $font-base;
      font-size: 1.8rem;
      font-weight: bold;
      color: $color-primary;
      line-height: 1.5;
      margin-bottom: 0.5em;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &__excerpt {
      font-size: 1.4rem;
      color: $color-dark;
      line-height: 1.6;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &__load-more-wrapper {
      text-align: center;
      margin-top: 6rem;
    }
  }

  .news-article {
    &__figure {
      width: 100%;
      max-width: 100%;
      max-height: 480px;
      margin-bottom: 3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
    }

    &__image {
      height: 100%;
      max-height: 480px;
      width: auto;
      object-fit: contain;
      cursor: pointer;
    }
  }