@use "../abstracts" as *;

.main-content--subpage {
  padding: 8rem 0 10rem;
  background-color: $color-white;

  .content-area {
    display: flex;
    gap: 3rem;

    .post-content {
      flex-grow: 1;
    }
  }

  .content-section {
    margin-bottom: 12rem;

    &:last-child {
      margin-bottom: 0;
    }

    .section-layout {
      display: flex;
      gap: 30px;
      align-items: flex-start;

      .section__image-wrapper {
        flex: 0 0 40%;
        aspect-ratio: 16/9;
        border-radius: 16px;
        overflow: hidden;
      }

      .section__image {
        max-width: 100%;
        height: auto;
        display: block;
      }

      .section__content-wrapper {
        flex: 1;
      }
    }

    &:nth-child(odd) {
      &.has-image .section-layout {
        flex-direction: row-reverse;
      }
    }

    &:not(.has-image) {
      .section__content-wrapper {
        flex-basis: 100%;
      }
    }
  }

  .section-title--left-align {
    text-align: left;
    padding-bottom: 1rem;
    border-bottom: 2px solid $color-primary;
    margin-bottom: 2.5rem;
    font-size: $font-size-h2;

    @include sp {
      font-size: $font-size-h2 * 0.7;
    }
  }

  .subsection-title {
    font-family: $font-base;
    font-size: $font-size-h2;
    font-weight: bold;
    color: $color-primary;
    margin-bottom: 2rem;
    padding-left: 1.5rem;
    border-left: 10px solid $color-secondary;

    @include sp {
      font-size: $font-size-h2 * 0.7;
    }
  }
}
